.post {

  &__header {
    margin-top: rem(30);
    text-align: center;
  }

  &__meta {
    padding-bottom: rem(30);
  }

  &__content {
    position: relative;
    padding-top: rem(50);
    border-top: 1px solid $hairline;
  }

  pre {
    @include media('small-only') {
      margin: rem(30) -1em;
    }

    @include media('medium') {
      margin: rem(30) rem(-50);
      padding: 0 rem(30);
    }
  }

  &__updated {
    font-size: rem(10);
    font-family: $heading-font-family;
    // margin-left: rem(10);
    padding: rem(5) rem(8);
    border-radius: 2px;
    background-color: #fff6bf;
    color: #8c7800;

    &--block {
      display: inline-block;
      font-size: rem(12);
      padding: 1rem;
      margin-bottom: rem(30);
    }
  }
}

.meta-tag {
  &__list {
    font-size: rem(14);
    font-family: $heading-font-family;
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    margin: rem(10) 0 0;
  }

  &__item {
    display: inline-block;
  }
}